import React from 'react';
import './App.css';
import {Container, Nav, Navbar, Row, Col} from 'react-bootstrap';
import bg from './img/bg.png';
import { lazy, Suspense, useEffect, useState } from 'react';
import data from './data.js';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';

import Detail from './routes/Detail';
import Cart from './routes/Cart';


function App() {

  useEffect(()=>{
    localStorage.setItem('watched', JSON.stringify([]))
  },[])

  let [shoes, setShoes] = useState(data);
  let [num, setNum] = useState(2);
  let navigate = useNavigate();

  let result = useQuery('', ()=>axios.get('https://codingapple1.github.io/userdata.json').then((a)=>{
    return a.data
  }))
  return (
    <div className="App">
      <Navbar className="navbar" bg="dark" variant="dark">
        <Container className='slide'>
        <Navbar.Brand href="/">Aible</Navbar.Brand>
        <Nav className="me-auto">
          <Nav.Link onClick={()=>{ navigate('/') }}>Home</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/detail/1') }}>detail</Nav.Link>
          <Nav.Link onClick={()=>{ navigate('/cart') }}>cart</Nav.Link>
        </Nav>
        <Nav className='ms-auto'></Nav>
        </Container>
      </Navbar>
      <div className='content'>
      <Suspense fallback={<div></div>}>
        <Routes>
        <Route path='/' element={
          <>
          <div className='scroll'>
            <div className='main-bg' style={{backgroundImage : 'url(' + bg + ')'}}></div>
          </div>
          
          <div className="container">
            <div className="row">
              { shoes.map(function (a){
                  return( <Card shoes={a}/> )
                })}
            </div>
          </div>
          {
            num<4 && <button onClick={()=>{
              console.log(num);
              axios.get('https://codingapple1.github.io/shop/data'+num+'.json')
              .then((result)=>{
                console.log(result.data)
                let copy = [...shoes, ...result.data];
                setShoes(copy);
              })
              .catch(()=>{
                console.log(' ')
              })
              setNum(num+1);
            }}>Click</button>
          }
          
          </>
        }/>
        <Route path='/detail/:id' element={
          
            <Detail shoes={shoes}/>
          
        } />
        <Route path='/cart' element={<Cart/>} />
        <Route path='*' element={<div> </div>}/>
      </Routes>
      </Suspense>
      
    </div>
      </div>
      
  );
}

function Card(props){
  return(
    <div className="col-md-4">
      <img src={"https://codingapple1.github.io/shop/shoes"+ (props.shoes.id+1) + ".jpg"} width="80%"/>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.price}</p>
    </div>
  )
}
export default App;
